<template>
    <div
        v-if="init"
        id="app">
        <router-view />
    </div>
</template>
<script>
export default {
    data() {
        return {
            init: true
        };
    },
    created() {
        this.$store.dispatch('getInfo').then((r) => {
            this.$store.commit('updateInfo', r.data);
            this.init = true;
        });
    }
};
</script>
<style lang="less">
#app{overflow: hidden;background-color: #1a0043;min-width: 750px;height:100vh}
</style>
