<template>
    <transition name="wk-loading">
        <div
            v-if="show"
            id="wk-loading">
            <div class="wk-loading__mask-transparent"/>
            <div class="wk-loading__toast">
                <i class="wk-loading__icon"/>
                <p class="wk-loading__content">{{ content }}</p>
            </div>
        </div>
    </transition>
</template>
<style lang="sass" scoped>
@import './index.scss';
</style>

<script>
export default {
    name: 'Loading',
    data() {
        return {
            show: true,
            content: '加载中'
        };
    },
    methods: {
        close() {
            this.show = false;
        }
    }
};
</script>
