import axios from 'axios';
import qs from 'qs';
import Loading from '@/libs/components/wkUI/packages/loading/index.js';
import Dialog from '@/libs/components/wkUI/packages/dialog/index.js';

// 创建axios实例
const instance = axios.create({
    baseURL: window.location.origin.indexOf('test') > -1 ? '/' : process.env.BASE_API, // api的base_url
    responseType: 'json',
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json'
    }
});

// beforeSend
instance.interceptors.request.use((config) => {
    if (config.isLoading) {
        Loading();
    }
    return config;
},
(error) => {
    if (error.config.isLoading) {
        Loading.close();
    }
    if (error.config.isInformation) {
        Dialog({
            mode: 'alert',
            content: '网络错误',
            confirmText: '知道了'
        });
    }
});

// complete
instance.interceptors.response.use((response) => {
    if (response.config.isLoading) {
        Loading.close();
    }
    if (response.config.returnOriginData) {
        return response.data;
    }
    if (response.data.code) {
        switch (response.data.code) {
            case '00000':
            case '0000':
            case '0':
            case '20000':
                return response.data;
            default:
                return Promise.reject(response.data);
        }
    } else {
        return response.data;
    }
},
(error) => {
    if (error.config.isLoading) {
        Loading.close();
    }
    if (error.config.isInformation) {
        Dialog({
            mode: 'alert',
            content: '网络错误',
            confirmText: '知道了'
        });
    }
    return Promise.reject(error);
});

class Ajax {
    /**
     * isLoading: 请求时是否展示loading框
     * isInformation： 请求异常是否展示请求异常弹框
     * returnOriginData：是否返回原始数据，即不对code进行判断成功,不进行promise.reject
     * noCache：请求添加时间戳，避免get缓存问题
     */
    get(url, params, {
        headers = { 'Content-Type': 'application/json' }, isLoading = true, isInformation = true, returnOriginData = false, noCache = false, timeout = 15000, ...options
    } = {}) {
        return instance.request({
            method: 'get',
            url,
            params: { ...params, ...(noCache ? { t: Date.now() } : {}) },
            timeout,
            headers,
            isLoading,
            returnOriginData,
            isInformation,
            ...options
        });
    }

    post(url, data, {
        headers = { 'Content-Type': 'application/x-www-form-urlencoded' }, isLoading = true, isInformation = true, returnOriginData = false, timeout = 15000, ...options
    } = {}) {
        switch (headers['Content-Type']) {
            case 'multipart/form-data': {
                const param = new FormData();
                for (const prop in data) {
                    if (Object.prototype.hasOwnProperty.call(data, prop)) {
                        param.append(prop, data[[prop]]);
                    }
                }
                data = param;
                break;
            }
            case 'application/x-www-form-urlencoded':
                data = qs.stringify(data);
                break;
            case 'application/json':
            default:
                break;
        }
        return instance.request({
            method: 'post',
            url,
            data,
            timeout,
            headers,
            isLoading,
            returnOriginData,
            isInformation,
            ...options
        });
    }

    request(options) {
        return instance.request(options);
    }
}

export default new Ajax();
