import Vue from 'vue';
import { merge, is } from '@/libs/utils/utils';
import Loading from './index.vue';

// 默认配置
const def = {
    content: '加载中',
    show: true,
    target: document.body
};
const LoadingCtr = Vue.extend(Loading);
const instance = new LoadingCtr();


const showLoading = function(options) {
    instance.$mount();
    const finalOpt = merge(instance.$data, merge(def, options));
    const { target } = finalOpt;

    if (is.dom(target)) {
        target.appendChild(instance.$el);
    } else if (is.str(target) && is.dom(document.querySelector(target))) {
        document.querySelector(target).appendChild(instance.$el);
    }
};
showLoading.close = function() {
    instance.close();
};


export default showLoading;
