export default {
    methods: {
        clickStat(evenetId, params) {
            if (window.KTAH5 && window.KTAH5.clickStat) {
                window.KTAH5.clickStat(evenetId, params);
            } else {
                this.$sentry && this.$sentry.captureMessage('没有在window上挂载KTAH5');
            }
        }
    }
};
