
// 判断类型
export const is = {
    /**
     * 判断参数是否是数组
     * @param {Random} a
     * @return {Boolean}
     */
    arr(a) { return Array.isArray(a); },
    /**
     * 判断参数是否是对象
     * @param {Random} a
     * @return {Boolean}
     */
    obj(a) { return Object.prototype.toString.call(a).indexOf('Object') > -1; },
    /**
     * 判断参数是否是svg
     * @param {Random} a
     * @return {Boolean}
     */
    svg(a) { return a instanceof SVGElement; },
    /**
     * 判断参数是否是原生dom节点
     * @param {Random} a
     * @return {Boolean}
     */
    dom(a) { return a.nodeType || is.svg(a); },
    /**
     * 判断参数是否是Number
     * 此方法将能转换成Number的字符串类型判断为否
     * @param {Random} a
     * @return {Boolean}
     */
    num(a) { return !Number.isNaN(a) && typeof a !== 'string'; },
    /**
     * 判断参数是否是String类型
     * @param {Random} a
     * @return {Boolean}
     */
    str(a) { return typeof a === 'string'; },
    /**
     * 判断参数是否是函数 function
     * @param {Random} a
     * @return {Boolean}
     */
    fnc(a) { return typeof a === 'function'; },
    /**
     * 判断参数是否是undefined
     * @param {Random} a
     * @return {Boolean}
     */
    und(a) { return typeof a === 'undefined'; },
    /**
     * 判断参数是否是null
     * @param {Random} a
     * @return {Boolean}
     */
    nul(a) { return a === null; }
};

/**
 * 返回一个闭区间内的随机整数
 * @param {Number} min 较小值
 * @param {Number} max 较大值
 * @return {Number}
 */
export function random(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

/**
 * 合并两个对象
 * 相同Key值以o2为基准，此方法会改变o1的值，返回合并后的o1
 * @param {Object} o1
 * @param {Object} o2
 * @return {Object}
 */
export function merge(o1, o2) {
    for (const p in o2) {
        if ({}.hasOwnProperty.call(o2, p)) {
            o1[p] = !is.und(o2[p]) ? o2[p] : o1[p];
        }
    }
    return o1;
}
/**
 * 深拷贝
 * 克隆对象,此方法返回一个新对象
 * @param {Object} o 需要复制的对象
 * @return {Object}
 */
export function clone(o) {
    const newObject = {};
    for (const p in o) {
        if ({}.hasOwnProperty.call(o, p)) {
            newObject[p] = o[p];
        }
    }
    return newObject;
}
/**
 * 将类数组对象转换为数组
 * @param {Object|NodeList|Array|} o
 * @return {Array}
 */
export function toArray(o) {
    if (is.arr(o)) return o;
    if (o instanceof NodeList || o instanceof HTMLCollection) return [].slice.call(o);
    if (is.obj(o)) {
        const arr = [];
        for (const key in o) {
            if (Object.prototype.hasOwnProperty.call(o, key)) {
                arr.push({ key, ...o[key] });
            }
        }
        return arr;
    }
    return [o];
}
/**
 * 数组中的keyName值转为key
 */
/**
 * 数组去重
 * @param {Array} a
 * @return {Array}
 */
export function unique(a) {
    return Array.from(new Set(a));
}

/**
 * 获取url上的参数
 * 如果url上没有此参数，返回''
 * @param {String} name 参数名字
 * @return {String} url上对应key值的参数
 */
export function getQueryString(name) {
    const reg = new RegExp('[?&]' + name + '=([^&]*)($|&)', 'i');
    const result1 = window.location.search.match(reg);
    const result2 = window.location.hash.match(reg);
    const result = result1 || result2;
    if (result != null) {
        const val = result[1];
        if (val.indexOf('#') !== -1) {
            return decodeURIComponent(val.split('#')[0]);
        }
        return decodeURIComponent(val);
    }
    return '';
}

/**
 * obj序列化为url参数
 * @param {Object} obj
 * @return {String}
 */
export function objToUrl(obj) {
    const arr = [];

    for (const o in obj) {
        if (Object.prototype.hasOwnProperty.call(obj, o)) {
            arr.push(encodeURIComponent(o) + '=' + encodeURIComponent(obj[o]));
        }
    }
    return arr.join('&');
}
/**
 * url参数反序列化为obj
 * @param {String} obj
 * @return {Object}
 */
export function urlToObj(url) {
    const strings = url.split('&');
    const obj = {};

    for (let i = 0; i < strings.length; i++) {
        const str = strings[i].split('=');

        if (str[0] !== '') {
            const [key, value] = str;
            obj[key] = value;
        }
    }
    return obj;
}

/**
 * 判断浏览器环境
 * 返回是否为微信|安卓|iOS
 * @return {Object}
 */
export function checkUA() {
    let isIOS;
    let isAndroid;
    let isWeChat;
    const ua = navigator.userAgent.toLowerCase();
    if (/iphone|ipad|ipod/.test(ua)) {
        isIOS = true;
        isAndroid = false;
    } else if (/android/.test(ua)) {
        isIOS = false;
        isAndroid = true;
    }
    if (/micromessenger/.test(ua)) {
        isWeChat = true;
    }
    return {
        isIOS,
        isAndroid,
        isWeChat
    };
}

/**
 * 页面用time毫秒滚动到指定number位置
 * @param {String} number 滚动高度值
 * @param {Number} time 毫秒
 */
export function scrollToPos(number = 0, time) { // 页面滚动到指定位置
    if (!time) {
        document.body.scrollTop = number;
        document.documentElement.scrollTop = number;
        return;
    }
    const spacingTime = 20; // 设置循环的间隔时间  值越小消耗性能越高
    let spacingInex = time / spacingTime; // 计算循环的次数
    let nowTop = document.body.scrollTop + document.documentElement.scrollTop; // 获取当前滚动条位置
    const everTop = (number - nowTop) / spacingInex; // 计算每次滑动的距离
    const scrollTimer = setInterval(() => {
        if (spacingInex > 0) {
            spacingInex--;
            scrollToPos(nowTop += everTop);
        } else {
            clearInterval(scrollTimer); // 清除计时器
        }
    }, spacingTime);
}

/**
 * prefixStyle根据浏览器不同，为样式添加前缀
 * @param {String} style 样式名
 * @return {String} 包含指定浏览器前缀的样式名
 */
const elementStyle = document.createElement('div').style;
// IIFE
const vendor = (() => {
    const transformNames = {
        webkit: 'webkitTransform',
        Moz: 'MozTransform',
        O: 'OTransform',
        ms: 'msTransform',
        standard: 'transform'
    };

    for (const key in transformNames) {
        if (elementStyle[transformNames[key]] !== undefined) {
            return key;
        }
    }
    return false;
})();
export function prefixStyle(style) {
    if (vendor === false) {
        return false;
    }

    if (vendor === 'standard') {
        return style;
    }

    return vendor + style.charAt(0).toUpperCase() + style.substr(1);
}

/**
 * ModalHelper解决遮罩层滚动穿透问题，分别在遮罩层弹出后和关闭前调用
 * ModalHelper.afterOpen() 弹层打开时调用
 * ModalHelper.beforeClose() 弹层关闭时调用
 */
export const ModalHelper = ((bodyCls) => {
    let scrollTop;
    return {
        afterOpen () {
            scrollTop = getPageScrollTop();
            // ({ scrollTop } = document.scrollingElement);
            document.body.classList.add(bodyCls);
            document.body.style.position = 'fixed';
            document.body.style.top = -scrollTop + 'px';
        },
        beforeClose () {
            document.body.classList.remove(bodyCls);
            // scrollTop lost after set position:fixed, restore it back.
            document.body.style.position = 'relative';
            document.body.style.top = 0 + 'px';
            setPageScrollTop(scrollTop);
        }
    };
})('dialog-open');

/**
 * 判断是否有类名
 * @param {Object} el dom元素
 * @param {String} className 类名
 */
export function hasClass(el, className) {
    const reg = new RegExp('(^|\\s)' + className + '(\\s|$)');
    return reg.test(el.className);
}

/**
 * 添加类名
 * @param {Object} el dom元素
 * @param {String} className 类名
 */
export function addClass(el, className) {
    if (hasClass(el, className)) {
        return;
    }
    let newClass = el.className.split(' ');
    newClass.push(className);
    newClass = newClass.join(' ');
    el.className = newClass;
}

/**
 * 删除类名
 * @param {Object} el dom元素
 * @param {String} className 类名
 */
export function removeClass(el, className) {
    if (hasClass(el, className)) {
        const reg = new RegExp('(^|\\s)' + className + '(\\s|$)');
        el.className = el.className.replace(reg, '');
    }
}

/*
 * 对Date的扩展，将 Date 转化为指定格式的String
 * 月(M)、日(D)、小时(h)、分(m)、秒(s)可以用 1-2 个占位符，
 * 年(Y)可以用 1-4 个占位符
 * @param {Date} date 日期
 * @param {String} fmt 格式化样式
 * 例子：
 * formatDate(new Date(), "YYYY-MM-DD hh:mm:ss") ==> 2018-07-02 08:09:04
 * formatDate(new Date(), "YYYY-MM-DD h:m:s")    ==> 2018-7-2 8:9:4
 * */
export function formatDate(date, fmt) {
    if (/(Y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    const o = {
        'M+': date.getMonth() + 1,
        'D+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
    };
    for (const k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
            const str = o[k] + '';
            fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str));
        }
    }
    return fmt;
}
function padLeftZero(str) {
    return ('00' + str).substr(str.length);
}

/**
 * 获取url上的search参数并转对象
 * 如果url上没有此参数，返回''
 * @param {String} url 参数名字
 * @return {Object} url上的search参数对象
 */
export function getUrlQueryObj(url) {
    let search;
    const res = {};
    if (url) {
        // eslint-disable-next-line no-useless-escape
        const oLocation = url.match(/\w+:\/\/((?:[\w-]+\.)+\w+)(?::\d+)?(\/[^\?\\"'\|:<>]*)?(?:\?([^'"\\<>#]*))?(?:#(\w+))?/i) || [];
        [, , , search] = oLocation; // 网址?后的search字段
    } else {
        search = window.location.search.substr(1); // 获取?后的search字段
    }

    if (typeof search === 'undefined') {
        return res;
    }

    for (let aSearch = search.split('&'), i = 0, len = aSearch.length; i < len; i++) {
        if (aSearch[i].indexOf('=') !== -1) {
            const keyLength = aSearch[i].indexOf('=');
            const key = aSearch[i].slice(0, keyLength);
            const value = aSearch[i].slice(keyLength + 1);
            res[key] = value;
        }
    }
    return res;
}

/**
 * 把当前链接里的所有search参数，都添加到指定链接link
 * @param {String} link 指定链接
 * @return {String} 添加完参数后的链接
 */
export function addAllParams(link) {
    const paramsObj = getUrlQueryObj();
    const params = objToUrl(paramsObj);
    if (params) {
        const hash = link.split('#')[1] || '';
        [link] = link.split('#');
        if (hash) {
            link = link + (link.indexOf('?') > -1 ? '&' : '?') + params + '#' + hash;
        } else {
            link = link + (link.indexOf('?') > -1 ? '&' : '?') + params;
        }
    }
    return link;
}

/**
 * 移除链接里指定参数
 * @param {String} link 指定链接
 * @param {String} param 要移除的参数
 * @return {String} 移除参数后的链接
 */
export function removeParam(link, param) {
    const paramsObj = getUrlQueryObj(link);
    delete paramsObj[param];
    const params = objToUrl(paramsObj);
    const hash = link.split('#')[1] || '';
    // eslint-disable-next-line no-useless-escape
    const [, hostname, pathname] = link.match(/\w+:\/\/((?:[\w-]+\.)+\w+)(?::\d+)?(\/[^\?\\"'\|:<>]*)?(?:\?([^'"\\<>#]*))?(?:#(\w+))?/i);
    if (hash) {
        link = window.location.protocol + '//' + hostname + pathname + '?' + params + '#' + hash;
    } else {
        link = window.location.protocol + '//' + hostname + pathname + '?' + params;
    }

    return link;
}

/**
 * 在链接里添加指定参数
 * @param {String} link 指定链接
 * @param {Object} addObj 需添加的参数对象
 */
export function addParamObj(link, addObj) {
    for (const i in addObj) {
        if (!addObj[i]) {
            delete addObj[i];
        }
    }
    const paramsObj = getUrlQueryObj(link);
    const allObj = Object.assign({}, paramsObj, addObj);
    const params = objToUrl(allObj);
    const hash = link.split('#')[1] || '';
    // eslint-disable-next-line no-useless-escape
    const arr = link.match(/\w+:\/\/((?:[\w-]+\.)+\w+)(?::\d+)?(\/[^\?\\"'\|:<>]*)?(?:\?([^'"\\<>#]*))?(?:#(\w+))?/i);
    if (arr && arr.length > 0) {
        const [, hostname, pathname] = arr;
        if (hash) {
            link = window.location.protocol + '//' + hostname + pathname + '?' + params + '#' + hash;
        } else {
            link = window.location.protocol + '//' + hostname + pathname + '?' + params;
        }
    }
    return link;
}


/* \
|*|  操作cookie
|*|  :: cookies.js ::
|*|
|*|  文档网址 https://developer.mozilla.org/zh-CN/docs/Web/API/Document/cookie
|*|
|*|  A complete cookies reader/writer framework with full unicode support.
|*|
|*|  https://developer.mozilla.org/en-US/docs/DOM/document.cookie
|*|
|*|  This framework is released under the GNU Public License, version 3 or later.
|*|  http://www.gnu.org/licenses/gpl-3.0-standalone.html
|*|
|*|  Syntaxes:
|*|
|*|  * docCookies.setItem(name, value[, end[, path[, domain[, secure]]]])
|*|  * docCookies.getItem(name)
|*|  * docCookies.removeItem(name[, path], domain)
|*|  * docCookies.hasItem(name)
|*|  * docCookies.keys()
|*|
\ */

export const docCookies = {
    getItem (sKey) {
        return decodeURIComponent(document.cookie.replace(new RegExp('(?:(?:^|.*;)\\s*' + encodeURIComponent(sKey).replace(/[-.+*]/g, '\\$&') + '\\s*\\=\\s*([^;]*).*$)|^.*$'), '$1')) || null;
    },
    setItem (sKey, sValue, vEnd, sPath, sDomain, bSecure) {
        // eslint-disable-next-line no-useless-escape
        if (!sKey || /^(?:expires|max\-age|path|domain|secure)$/i.test(sKey)) { return false; }
        let sExpires = '';
        if (vEnd) {
            switch (vEnd.constructor) {
                case Number:
                    sExpires = vEnd === Infinity ? '; expires=Fri, 31 Dec 9999 23:59:59 GMT' : '; max-age=' + vEnd;
                    break;
                case String:
                    sExpires = '; expires=' + vEnd;
                    break;
                case Date:
                    sExpires = '; expires=' + vEnd.toUTCString();
                    break;
                default:
                    break;
            }
        }
        document.cookie = encodeURIComponent(sKey) + '=' + encodeURIComponent(sValue) + sExpires + (sDomain ? '; domain=' + sDomain : '') + (sPath ? '; path=' + sPath : '') + (bSecure ? '; secure' : '');
        return true;
    },
    removeItem (sKey, sPath, sDomain) {
        if (!sKey || !this.hasItem(sKey)) { return false; }
        document.cookie = encodeURIComponent(sKey) + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT' + (sDomain ? '; domain=' + sDomain : '') + (sPath ? '; path=' + sPath : '');
        return true;
    },
    hasItem (sKey) {
        return (new RegExp('(?:^|;\\s*)' + encodeURIComponent(sKey).replace(/[-.+*]/g, '\\$&') + '\\s*\\=')).test(document.cookie);
    },
    // eslint-disable-next-line object-shorthand
    keys: /* optional method: you can safely remove it! */ function () {
        // eslint-disable-next-line no-useless-escape
        const aKeys = document.cookie.replace(/((?:^|\s*;)[^\=]+)(?=;|$)|^\s*|\s*(?:\=[^;]*)?(?:\1|$)/g, '').split(/\s*(?:\=[^;]*)?;\s*/);
        for (let nIdx = 0; nIdx < aKeys.length; nIdx++) { aKeys[nIdx] = decodeURIComponent(aKeys[nIdx]); }
        return aKeys;
    }
};

export function toThousands(num) {
    let result = '';
    let counter = 0;
    num = (num || 0).toString();
    for (let i = num.length - 1; i >= 0; i--) {
        counter++;
        result = num.charAt(i) + result;
        if (!(counter % 3) && i !== 0) { result = ',' + result; }
    }
    return result + '.00';
}
export function b64Encode(str) {
    return btoa(encodeURIComponent(str));
}

export function b64Decode(str) {
    return decodeURIComponent(atob(str));
}
/**
 * @description: 发送jsonp请求
 * @param {String}
 * @return:
 */
export function jsonpRequest(url) {
    const script = document.createElement('script');
    script.setAttribute('type', 'text/javascript');
    script.setAttribute('src', url);
    document.head.appendChild(script);
}
/**
 * @description: 判断字符串是不是json
 * @param {String}
 * @return:
 */
export function isJson(str) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

/**
* 获取页面滚动高度
*/
export function getPageScrollTop () {
    if (document.scrollingElement) {
        return document.scrollingElement.scrollTop;
    }

    return Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop);
}

/**
* 设置页面滚动高度
* @param {Number} top 滚动高度
*/
export function setPageScrollTop (top) {
    top = Number(top, 10) || 0;
    if (document.scrollingElement) {
        document.scrollingElement.scrollTop = top;
    } else {
        document.documentElement.scrollTop = top;
        document.body.scrollTop = top;
    }
}


/**
 * 像素转rem(px2rem)
 * @param {String | Number} pxNum 像素值
 * @param {Number} winWidth 屏幕宽度基准
 */
export function pxToRem(pxNum, winWidth = 750) {
    const px = Number.parseInt(pxNum, 10);
    return px / winWidth * 10 + 'rem';
}
