import Router from 'vue-router';
import Vue from 'vue';
// import { JSAPI_default } from '@/libs/wx/index.js';

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/',
            redirect: '/index'
        },
        {
            path: '/index',
            component: () => import('../views/home.vue'),
            meta: {
                title: '4周年',
                pvTitle: '4周年'
            }
        }
    ]
});
