<template>
    <transition name="wk-dialog">
        <div
            v-if="show"
            id="wk-dialog">
            <div
                class="wk-dialog-mask"
                @click="maskClick"/>
            <div
                :class="{'show-icon': showIcon}"
                class="wk-dialog-contentBox">
                <div
                    v-if="showIcon"
                    class="wk-dialog-iconBox">
                    <div
                        :class="iconType"
                        class="wk-dialog-icon"
                        alt=""/>
                </div>
                <div
                    v-if="showTitle"
                    class="wk-dialog-titleBox">
                    <div
                        class="wk-dialog-titleContent">{{ title }}</div>
                </div>
                <div
                    :class="{ 'no-title':(!showTitle) }"
                    class="wk-dialog-hintBox">
                    <div
                        :style="{textAlign: textAlign}"
                        class="wk-dialog-hintContent"
                        v-html="content"/>
                </div>
                <div class="wk-dialog-btnBox">
                    <div
                        v-if="mode == 'confirm'"
                        class="wk-dialog-ltBtn wk-dialog-btn"
                        @click="handleCancel">{{ cancelText }}</div>
                    <div
                        class="wk-dialog-rtBtn wk-dialog-btn"
                        @click="handleOk">{{ confirmText }}</div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import { ModalHelper } from '@/libs/utils/utils';

export default {
    name: 'Dialog',
    data() {
        return {
            show: true,
            showIcon: false,
            iconType: 'ok',
            showTitle: true,
            textAlign: 'center',
            title: '提示',
            content: '',
            mode: 'alert',
            cancelText: '取消',
            confirmText: '确定',
            listenMaskClick: false
        };
    },
    methods: {
        close () {
            this.show = false;
            ModalHelper.beforeClose();
        },
        handleCancel() {
            this.close();
        },
        handleOk() {
            this.close();
        },
        maskClick() {
            if (this.listenMaskClick) {
                this.close();
            }
        }
    }
};
</script>
<style lang="scss" scoped>
    @import './index.scss'
</style>
