import Vue from 'vue';
import {
    merge, is, clone, ModalHelper
} from '@/libs/utils/utils';
import Dialog from './index.vue';

// icon: 'ok'|'err'|'neterr'
// mode: 'alert'|'confirm''
const def = {
    show: true,
    showIcon: false,
    iconType: 'neterr',
    showTitle: false,
    title: '提示',
    content: '',
    textAlign: 'center',
    mode: 'confirm',
    cancelText: '取消',
    confirmText: '确定',
    listenMaskClick: false,
    target: document.body
};
const DialogConstructor = Vue.extend(Dialog);
const instance = new DialogConstructor();

// 入口函数
const showDialog = function(options) {
    instance.$mount();
    const defOptions = clone(def);
    const finalOpt = merge(instance.$data, merge(defOptions, options));
    const { target } = finalOpt;
    if (is.dom(target)) {
        target.appendChild(instance.$el);
    } else if (is.str(target) && is.dom(document.querySelector(target))) {
        document.querySelector(target).appendChild(instance.$el);
    }

    ModalHelper.afterOpen();

    return new Promise((resolve, reject) => {
        instance.handleOk = () => {
            instance.close();
            resolve();
        };
        instance.handleCancel = () => {
            instance.close();
            reject();
        };
    });
};
// 暴露的方法
showDialog.close = function() {
    instance.close();
};
export default showDialog;
